.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 1000px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

.card {
    margin-top: 15px;
    display: flex;
    box-shadow: 0px 3px 5px 4px rgba(0, 0, 0, 0.2);
}

.info {
    padding: 15px;
    display: flex;
    flex-direction: column;
}

.btn {
    width: 90px;
    display: block;
    padding: 5px 8px;
    text-decoration: none;
    border-radius: 2px;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.18);
    cursor: pointer;
}


